import { Component, Injector, OnInit } from '@angular/core';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { TranslateProvider } from '@nequi/features/commons';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  AuthorizationInteractor,
  GetApprovalByUserEntity,
  ManageAuthorizationParams
} from '@nequi/features/open-banking/core';
import { LoadingService, ToastService } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

import { OpenBankingConfig } from '../../../../config/open-banking.config';
import { ExperiencesConfig } from './experiences.config';

@Component({
  selector: 'nequi-home',
  templateUrl: './experiences.page.html',
  styleUrls: ['./experiences.page.scss'],
})
export class ExperiencesPage extends FeatureBasePage implements OnInit {
  public showRetry = false;
  public showEmptyCase = false;
  public permissionList: GetApprovalByUserEntity[] = [];
  private acceptContractMail = false;

  constructor(
    injector: Injector,
    private alertService: AlertService,
    private loadingService: LoadingService,
    private authorizationInteractor: AuthorizationInteractor,
    private translateProvider: TranslateProvider,
    private toastService: ToastService,
    private applicationDataInteractor: ApplicationDataInteractor
  ) {
    super(injector);
  }

  public async ngOnInit() {
    await this.getPermissions();
  }

  public async getPermissions() {
    try {
      this.loadingService.show();
      this.permissionList = await this.authorizationInteractor.getApprovalByUser();
      await this.cleanPermissions();
      this.showEmptyCase = true;
      this.loadingService.hideWithoutFeedback();
    } catch (error) {
      this.showRetry = true;
      this.showEmptyCase = false;
      this.loadingService.hideWithoutFeedback();
      throw error;
    }
  }

  public async cleanPermissions() {
    this.permissionList = this.permissionList.filter(
      (permission) => permission.allowedPermissions.length !== 0
    );
  }

  public async deletePermission(permission: GetApprovalByUserEntity) {
    const alert = this.translateProvider.get(ExperiencesConfig.i18n.deleteAlert);

    this.alertService.show({
      messages: {
        header: alert.header,
        message: createFormat(alert.message, [permission.appName]),
        buttons: {
          accept: alert.buttons.accept,
          cancel: alert.buttons.cancel,
        },
      },
      handlers: {
        accept: async () => this.updatePermissionStatus(permission),
      },
    });
  }

  private async updatePermissionStatus(permission: GetApprovalByUserEntity) {
    try {
      this.loadingService.show();

      const params: ManageAuthorizationParams = {
        app: permission.app.slice(0, permission.app.lastIndexOf('_')),
        acceptContractMail: this.acceptContractMail,
        permissions: [],
      };

      await this.authorizationInteractor.unauthorized(params);

      await this.loadingService.hideWithoutFeedback();

      this.deletepermissionFromList(permission.app);

      this.applicationDataInteractor.remove(
        OpenBankingConfig.applicationData.persistenceDataKey + permission.appName
      );

      const message = this.translateProvider.get(ExperiencesConfig.i18n.deleteSuccess);

      this.toastService.showSuccess(message, ExperiencesConfig.toastDelay);
    } catch (error) {
      this.loadingService.hide(false);

      const alert = this.translateProvider.get(ExperiencesConfig.i18n.error);

      this.alertService.show({
        messages: {
          header: alert.title,
          message: alert.message,
          buttons: {
            accept: alert.button,
          },
        },
        handlers: {
          accept: () => {},
        },
      });
    }
  }

  private deletepermissionFromList(permissionApp: string) {
    this.permissionList = this.permissionList.filter(
      (permission) => permission.app !== permissionApp
    );
  }
}
