import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons/resolvers';

import { OpenBankingConfig } from '../../../config/open-banking.config';
import { BalanceInquiryDetailPage } from './balance-inquiry-detail/balance-inquiry-detail.page';
import { BalanceInquiryPage } from './balance-inquiry/balance-inquiry.page';
import { ExperiencesPage } from './experiences/experiences.page';

const routes: Routes = [
  {
    path: 'experiences',
    component: ExperiencesPage,
  },
  {
    path: 'balance-inquiry',
    component: BalanceInquiryPage,
  },
  {
    path: 'balance-inquiry-detail',
    component: BalanceInquiryDetailPage,
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(I18nModuleResolver.setup(routes, [OpenBankingConfig.i18n.moduleKey])),
  ],
  exports: [RouterModule],
})
export class PermissionRoutingModule {}
