import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { EmptyCaseException } from '@nequi/commons/exceptions';
import { TranslateProvider } from '@nequi/features/commons';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { AuthorizationInteractor } from '@nequi/features/open-banking/core';
import { LoadingService, ToastService } from '@nequi/ui';

import { BalanceInquiryService } from '../../services/balance-inquiry.service';
import { BalanceInquiryConfig } from './balance-inquiry.config';

@Component({
  selector: 'app-balance-inquiry',
  templateUrl: './balance-inquiry.page.html',
  styleUrls: ['./balance-inquiry.page.scss'],
})
export class BalanceInquiryPage extends FeatureBasePage implements OnInit {
  private isAuthorizationCreated: boolean;

  constructor(
    injector: Injector,
    public balanceInquiryService: BalanceInquiryService,
    private authorizationInteractor: AuthorizationInteractor,
    private loadingService: LoadingService,
    private toastService: ToastService,
    private translateProvider: TranslateProvider,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {
    super(injector);
  }

  public async ngOnInit() {
    this.isAuthorizationCreated = this.router.getCurrentNavigation().extras.state?.authorizationCreated;
    this.checkAuthorizationCreated();
    await this.getAuthorizationsList();
  }

  public goBack() {
    if (this.isAuthorizationCreated) {
      this.navigatorProvider.navigateRoot([BalanceInquiryConfig.routes.backpack], {
        replaceUrl: true,
      });
    } else {
      this.navigatorProvider.back();
    }
  }

  public async getAuthorizationsList() {
    try {
      await this.loadingService.show();
      this.balanceInquiryService.authorizationList = await this.authorizationInteractor.getAuthorizations();
      this.showListView();
      await this.loadingService.hideWithoutFeedback();
    } catch (exception) {
      await this.loadingService.hideWithoutFeedback();
      this.balanceInquiryService.authorizationList = [];

      if (exception instanceof EmptyCaseException) {
        this.showEmptyCaseView();

        return;
      }

      this.showReloadView();
    }
  }

  public goToBalanceInquiryDetail(authorization) {
    this.navigatorProvider.navigateForward([BalanceInquiryConfig.routes.balanceInquiryDetail], {
      state: authorization,
      relativeTo: this.activatedRoute,
    });
  }

  private showEmptyCaseView() {
    this.balanceInquiryService.isShowEmptyCase = true;
    this.balanceInquiryService.showRetry = false;
  }

  private showReloadView() {
    this.balanceInquiryService.isShowEmptyCase = false;
    this.balanceInquiryService.showRetry = true;
  }

  private showListView() {
    this.balanceInquiryService.isShowEmptyCase = false;
    this.balanceInquiryService.showRetry = false;
  }

  private checkAuthorizationCreated() {
    if (this.isAuthorizationCreated) {
      const message = this.translateProvider.get(
        BalanceInquiryConfig.i18n.authorizationSuccessMessage
      );

      this.toastService.showSuccess(message);
    }
  }
}
