import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateProvider } from '@nequi/features/commons';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  AuthInfo,
  AuthorizationInteractor,
  BalanceInquiryEntity
} from '@nequi/features/open-banking/core';
import { LoadingService, ToastService } from '@nequi/ui';

import { BalanceInquiryService } from '../../services/balance-inquiry.service';
import { BalanceInquiryDetailConfig } from './balance-inquiry-detail.config';

@Component({
  selector: 'app-balance-inquiry-detail',
  templateUrl: './balance-inquiry-detail.page.html',
  styleUrls: ['./balance-inquiry-detail.page.scss'],
})
export class BalanceInquiryDetailPage extends FeatureBasePage implements OnInit {
  public authorizationsList: BalanceInquiryEntity;

  constructor(
    injector: Injector,
    private balanceInquiryService: BalanceInquiryService,
    private alertService: AlertService,
    private loadingService: LoadingService,
    private authorizationInteractor: AuthorizationInteractor,
    private translateProvider: TranslateProvider,
    private router: Router,
    private toastService: ToastService
  ) {
    super(injector);
  }

  public async ngOnInit() {
    this.authorizationsList = this.router.getCurrentNavigation().extras.state as BalanceInquiryEntity;
  }

  public showAlertConfirmDelete(authorization: AuthInfo) {
    const message = this.translateProvider.get(BalanceInquiryDetailConfig.i18n.deleteConfigMessage, {
      commerce: this.authorizationsList.commerce,
      productName: authorization.productName,
    });

    const deleteConfig = this.translateProvider.get(BalanceInquiryDetailConfig.i18n.deleteConfig);

    this.alertService.show({
      messages: {
        ...deleteConfig,
        message,
      },
      handlers: {
        accept: () => this.deleteAuthorization(authorization),
      },
    });
  }

  private async deleteAuthorization(authorization: AuthInfo) {
    try {
      await this.loadingService.show();
      await this.authorizationInteractor.archiveAuthorization(authorization.authorizedAt);
      this.removeAuthorization(authorization);
      await this.loadingService.hide(true);
      this.showAlertSuccessDelete();
    } catch (error) {
      await this.loadingService.hide(false);
      this.showAlertErrorDelete();
    }
  }

  private removeAuthorization(authorization: AuthInfo) {
    const index = this.authorizationsList.authData.findIndex(
      (element: AuthInfo) => element.authorizedAt === authorization.authorizedAt
    );

    this.authorizationsList.authData.splice(index, 1);
    this.validateEmptyCase();
  }

  private async validateEmptyCase() {
    if (this.authorizationsList.authData.length === 0) {
      await this.removeCommerceAuthorization();
      this.balanceInquiryService.isShowEmptyCase = true;
      this.balanceInquiryService.showRetry = false;
      this.goBack();
    }
  }

  private removeCommerceAuthorization() {
    const index = this.balanceInquiryService.authorizationList.findIndex(
      (element: BalanceInquiryEntity) => element.commerce === this.authorizationsList.commerce
    );

    this.balanceInquiryService.authorizationList.splice(index, 1);
  }

  private showAlertErrorDelete() {
    const message = this.translateProvider.get(BalanceInquiryDetailConfig.i18n.deleteErrorMessage);

    this.toastService.showError(message);
  }

  private showAlertSuccessDelete() {
    const message = this.translateProvider.get(BalanceInquiryDetailConfig.i18n.deleteSuccessMessage);

    this.toastService.showSuccess(message);
  }
}
